* {
  box-sizing: border-box;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
  font-family: 'Luckiest Guy', cursive;
}
.card {
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    justify-content: center;
}

.card>.img-container {
    overflow: hidden;
    text-align: center;
    background: black;
    justify-content: center;  
    margin: 20px;   
}

.card>.img-container>img {
    width: 150px;   
}
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    text-align: center;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}
.title {
    width: 100%;
    text-align: center;
    color: black;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 0px;
}

.scoreSummary {
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
}

.row {
  margin: 30px; 
}
